<template>
  <div class="register-container">
    <el-card class="register-card">
      <div slot="header" class="clearfix">
        <h2></h2>
      </div>
      <el-form 
        ref="registerForm" 
        :model="registerForm" 
        :rules="rules" 
        label-width="100px"
        @submit.native.prevent="handleRegister"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="registerForm.username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input 
            v-model="registerForm.password" 
            type="password" 
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmPassword">
          <el-input 
            v-model="registerForm.confirmPassword" 
            type="password" 
            placeholder="" 
            @keyup.enter.native="handleRegister"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" :loading="loading" @click="handleRegister"></el-button>
          <el-button @click="goToLogin"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: 'Register',
  data() {
    // 
    const validateConfirmPassword = (rule, value, callback) => {
      if (value !== this.registerForm.password) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    
    return {
      registerForm: {
        username: '',
        password: '',
        confirmPassword: ''
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 20, message: '3-20', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateConfirmPassword, trigger: 'blur' }
        ]
      },
      loading: false
    };
  },
  methods: {
    ...mapActions(['register']),
    
    async handleRegister() {
      try {
        // 
        await this.$refs.registerForm.validate();
        
        this.loading = true;
        
        // 
        await this.register({
          username: this.registerForm.username,
          password: this.registerForm.password
        });
        
        // 
        this.$message.success('');
        this.$router.push('/');
      } catch (error) {
        if (error.response && error.response.data) {
          this.$message.error(error.response.data.message || '');
        } else if (!error.response) {
          // 
        } else {
          this.$message.error('');
        }
      } finally {
        this.loading = false;
      }
    },
    
    goToLogin() {
      this.$router.push('/login');
    }
  },
  // 
  beforeRouteEnter(to, from, next) {
    const token = localStorage.getItem('token');
    if (token) {
      next('/');
    } else {
      next();
    }
  }
};
</script>

<style scoped>
.register-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f5f7fa;
}

.register-card {
  width: 400px;
}

.register-card h2 {
  text-align: center;
  margin: 0;
  color: #409EFF;
}
</style> 